<template>
    <nacl-page-container no-header>
        <!-- <nacl-header
            transparent
            style="color: #fff;"
        /> -->
        <nacl-page-content no-header>
            <div class="background-circle" />
            <div
                class="img-name"
            >
                <div class="avatar">
                    <img src="@/assets/default-avatar.png">
                </div>
                <p class="text">欢迎登录幸运拍</p>
                <div class="user">{{ username }}</div>
            </div>
            <div class="content">
                <div class="order-type">
                    <nacl-flexbox class="plate">
                        <div>
                            <a @click="goOrder('?showType=1')">
                                <div class="img" />
                                <p>待付款</p>
                            </a>
                        </div>
                        <div>
                            <a @click="goOrder('?showType=2')">
                                <div class="img" />
                                <p>待发货</p>
                            </a>
                        </div>
                        <div>
                            <a @click="goOrder('?showType=3')">
                                <div class="img" />
                                <p>待收货</p>
                            </a>
                        </div>
                        <div>
                            <a @click="goOrder('?showType=4')">
                                <div class="img" />
                                <p>已完成</p>
                            </a>
                        </div>
                    </nacl-flexbox>
                </div>
                <div class="card middle-card">
                    <nacl-list class="list">
                        <nacl-list-item @click="goOrder('?showType=0')">
                            <img src="@/assets/my-icon-00.png">
                            <label>全部订单</label>
                        </nacl-list-item>
                        <nacl-list-item @click="goWinningRecord()">
                            <img src="@/assets/menu-03.png">
                            <label>中奖记录</label>
                        </nacl-list-item>
                        <nacl-list-item @click="goAddressList()">
                            <img src="@/assets/menu-01.png">
                            <label>地址管理</label>
                        </nacl-list-item>
                        <nacl-list-item @click="goCollect()">
                            <img src="@/assets/menu-02.png">
                            <label>我的收藏</label>
                        </nacl-list-item>
                        <!-- <nacl-list-item @click="$router.push('/order')">
                            <img src="@/assets/my-icon-03.png">
                            <label>历史记录</label>
                        </nacl-list-item> -->
                    </nacl-list>
                </div>
                <div class="card bottom-card">
                    <nacl-list class="list">
                        <nacl-list-item @click="goFeedback()">
                            <img src="@/assets/menu-05.png">
                            <label>意见反馈</label>
                        </nacl-list-item>
                        <nacl-list-item @click="logout">
                            <img src="@/assets/menu-06.png">
                            <label>退出登录</label>
                        </nacl-list-item>
                    </nacl-list>
                </div>
                <!-- <div
                    class="logout"
                    @click="logout"
                >
                    退出
                </div> -->
            </div>
            <!-- <privacy class="privacy" /> -->
        </nacl-page-content>
        <alter
            title="提示"
            text="请拨打400-6688-313"
            :alter-data="alterData"
            :is-show="isShowAlter"
            @clickButton="isShowAlter = false"
        />
    </nacl-page-container>
</template>

<script>
import { NaclList } from 'nacl-ui'
// import AV from 'leancloud-storage'
import { logout } from '../services/index'
import alter from '../components/alter'
import Privacy from '@/components/privacy'

export default {
    name: 'my',
    components: { NaclList, NaclListItem: NaclList.Item, alter, Privacy },
    data () {
        return {
            isShowAlter: false
        }
    },
    computed: {
        username () {
            const UserData = JSON.parse(localStorage.getItem('UserData'))
            return UserData.nickName
        }
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goWinningRecord(){
            this.toGetUser();
            window.webkit.messageHandlers.winningRecord.postMessage('');
        },
        goOrder(param){
            this.toGetUser();
            window.webkit.messageHandlers.order.postMessage(param);
        },
        goAddressList(){
            this.toGetUser();
            window.webkit.messageHandlers.addressList.postMessage('');
        },
        goCollect(){
            this.toGetUser();
            window.webkit.messageHandlers.collect.postMessage('');
        },
        goFeedback(){
            this.toGetUser();
            window.webkit.messageHandlers.feedback.postMessage('');
        },
        callCustomService () {
            // window.location.href = 'tel:400-6688-313'
            this.isShowAlter = true
        },
        logout () {
            logout()
            // this.$router.push('/login')
            this.toGetUser();
            window.webkit.messageHandlers.login.postMessage('');
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
.privacy {
  margin-top: 70px;
}
.nacl-list-item {
  padding: 34px 38px 38px 50px;
}

.background-circle {
  position: absolute;
  top: -80px;
  left: 0;
  height: 650px;
  width: 100%;
  z-index: 1;
  background: url("../assets/my-bg.png") no-repeat;
  background-size: 100%;
}
.content {
  position: relative;
  z-index: 1;
  margin: auto;
  padding-top: 90px;
}
.card {
  background: #fff;
  width: 682px;
  margin: 0 auto;
  border-radius: 16px;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.08);
  &.top-card {
    padding-top: 70px;
  }
  &.middle-card {
    position: relative;
    padding-top: 10px;
  }
  &.bottom-card {
    margin-top: 20px;
  }
}
.img-name {
  position: relative;
  z-index: 2;
  text-align: center;
  color: #ffff;
  .text {
      line-height: 1;
      font-size: 24px;
  }
}
.avatar {
  width: 156px;
  height: 156px;
  margin: auto;
  padding-top: 100px;
  padding-bottom: 15px;
  overflow: hidden;
  img {
    max-width: 100%;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
  }
}
.user {
  // margin-top: 30px;
  font-size: 38px;
  font-weight: 500;
  // color: #333;
  text-align: center;
  // padding: 0 0 33px;
  line-height: 1.5;
}
.list {
  border-radius: 12px;
  border: none;
  img {
    max-width: 48px;
    max-height: 48px;
  }
  label {
    margin-left: 44px;
    font-size: 32px;
  }
}
.sign-out {
  display: block;
  width: 100%;
  height: 80px;
  margin: auto;
  box-sizing: border-box;
  border: 0;
  border-radius: 10px;
  background: #fff;
  font-size: 100%;
  color: #999;
}
.plate {
  padding: 0px 0 10px;
  margin-bottom: 20px;
  > div {
    flex: 1;
    text-align: center;
    height: 114px;
    padding-bottom: 20px;
    background: rgba(250, 250, 250, 0.08);
    margin-right: 5px;
    &:last-child {
      margin-right: 0;
    }
    .img {
      width: 60px;
      height: 60px;
      margin: 15px auto 0;
      background: no-repeat;
      background-size: 100%;
      vertical-align: top;
    }
    &:nth-child(1) .img {
      background-image: url("../assets/icon-my-1.png");
    }
    &:nth-child(2) .img {
      background-image: url("../assets/icon-my-2.png");
    }
    &:nth-child(3) .img {
      background-image: url("../assets/icon-my-4.png");
    }
    &:nth-child(4) .img {
      background-image: url("../assets/icon-my-4.png");
    }
  }

  p {
    font-size: 28px;
    color: #fff;
    line-height: 1;
  }
}
.logout {
  margin: 20px 30px;
  height: 104px;
  background-color: #ffffff;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.08);
  border-radius: 16px;
  font-size: 32px;
  color: #999;
  text-align: center;
  line-height: 104px;
}
</style>
